
.footer {
	  background-color: #333;
	    color: #fff;
	      padding: 1rem;
	        text-align: center;
	}

	.footer-content {
		  max-width: 1200px;
		    margin: 0 auto;
	    }

	    .footer-links {
		      list-style: none;
		        padding: 0;
		}

		.footer-links li {
			  display: inline;
			    margin: 0 10px;
		    }

		    .footer-links a {
			      color: #fff;
			        text-decoration: none;
			}

			.footer-links a:hover {
				  text-decoration: underline;
			  }

